 

.header{
    height: 400px;
    background-image:url('https://i.ibb.co/k0fxXPD/terrace-cafe-city-beach-28466-2560x1080.jpg');
    background-repeat:no-repeat;
    background-size: cover;
    background-size: 100% 400px !important;
    margin: auto;
}
   
 

    

#logo{
    height:120px;
    width:120px;
    background-color:white !important;
    margin-left:45%;
    margin-top:10%;
    border-radius: 50%;
    font-size:80px;
    text-align: center;
    color:rgb(241, 136, 94)!important;
    animation: logoanimate 9s infinite linear;
    
}
@keyframes logoanimate{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

.form-select{
    width: 25% !important;
    font-size: 12px;
    height: 35px;
    margin-left: 6px;
    display: inline-block;
}
.headingFormat{
    color: white; 
    font-family:Georgia, 'Times New Roman', Times, serif; 
    font-weight: bold;
}
