
#header{
    height:60px;
    background-color:#d2a08c
}

#icon{
    font-size:30px;
    color:#13648c;
    margin-left: 1%;
    margin-top: 0%;
    font-weight: bold;
    position: absolute;
    margin-top: 5px;
}
#nav{
    float: left;
    margin-left: 20%;
}
#social{
    float: right;
    margin-right: 1%;
}
a{
    text-decoration: none
}
.slogo{
    height:50px;
    width:50px;
}

#divcontent{
    width: 90%;
    height: 450px;
    margin-top: 2%;
    margin-left: 2%;
}

#imgDiv{
    width: 45%;
    height: 380px;
    margin-top: 2%;
    margin-left: 2%;
    float: left;
}

#imgDiv>img{
    width: 100%;
    height: 100%;
}

#textDiv{
    width: 48%;
    height: 450px;
    margin-top: 1%;
    margin-left: 2%;
    float: left;
}

.checked{
    color:green;
}

.icon{
    display: inline-block;
    margin-left:2%
}

.stock{
    font-size: 23px;
    color:green
}
.btn{
    height: 40px;
    width: 200px;
    font-size:20px
}

.btn-checkout{
    background-color:rgb(52, 201, 52);
    color:white
}

.btn-atc{
    background-color:rgb(243, 62, 62);
    color:white
}
.cartBtn{
    width:50px;
    margin-left:0%;
}

.cart{
    margin-left: 80px;
    margin-top: 10px;
}

.menupix{
    margin-top: 10px;
}

.return{
    margin-right: 10px;
    box-shadow:10px 15px 20px rgb(48, 51, 53);
}

.checkout{
    margin-right: 10px;
    box-shadow:10px 15px 20px rgb(48, 51, 53);
}

.columnA{
    margin-bottom: 40px!important;
}

.accordion-button{
    color: black !important;
    background-color:white;
}

.accordion-body{
    background-color: rgb(217, 247, 222)!important;
}

